import React, { Component } from 'react';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { withStyles } from '@material-ui/core/styles';

const styles = {
  fab: {
    position: 'fixed',
    bottom: '20px',
    right: '20px'
  }
}

class NewCountry extends Component {
  state = {
    open: false,
    name: '',
  }
  handleOpen = () => {
    this.setState({ open: true, name: '' });
  }
  handleClose = () => {
    this.setState({ open: false });
  }
  handleChange = (e) => this.setState({ [e.target.name]: e.target.value});
  handleSave = () => {
    this.props.onAdd(this.state.name);
    this.setState({ open: false });
  }
  render() { 
    const { classes } = this.props;
    const { open } = this.state;
    return (
      <React.Fragment>
        <Fab 
          onClick={ this.handleOpen }
          className={ classes.fab } 
          size="medium" 
          color="primary" 
          aria-label="add">
          <AddIcon />
        </Fab>
        <Dialog open={ open } onClose={ this.handleClose } aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Add Country</DialogTitle>
        <DialogContent>
          <DialogContentText>
            To add a new country, enter the name
          </DialogContentText>
          <TextField
            onChange={ this.handleChange }
            autoFocus
            margin="dense"
            id="name"
            name="name"
            value={ this.state.name }
            label="Country Name"
            type="text"
            fullWidth
            autoComplete="off"
            autoCapitalize="off"
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={ this.handleClose } color="primary">
            Cancel
          </Button>
          <Button disabled={ this.state.name.trim().length === 0 } onClick={ this.handleSave } color="primary">
            Save
          </Button>
        </DialogActions>
      </Dialog>
    </React.Fragment>
    );
  }
}

export default withStyles(styles, { withTheme: true })(NewCountry);
